
import React, { useEffect, useState } from 'react';
import { getAuth, onAuthStateChanged } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import {firebase} from "../initFirebase"
import { getDatabase, ref, child, get } from "firebase/database";
import person from '../assets/icons/person.png'
export interface IAuthRouteProps {
    children: React.ReactNode;
}
const AuthRoute: React.FunctionComponent<IAuthRouteProps> = (props) => {
    const auth = getAuth();
    const children = props.children;
    const [loading, setLoading] = useState(false);
    console.log('auth route here...')
    useEffect(() => {
        const AuthCheck = onAuthStateChanged(auth, (user) => {
            if (user) {
                console.log(user.email)
                window.$userEmail = `${user.email}`
                // setLoading(false);
                // console.log("user", user.toJSON())
                
                const db = firebase.database();
                const usersRef = db.ref("users")
                usersRef.on("value", snapshot => {
                  const noo = snapshot.exportVal()
                  const newRef = noo[encodeURIComponent(`${user.email}`).replace(/\./g, '%2E')]
                  if (newRef !== undefined){
                    console.log('exitsts', newRef)
                    window.$userName = `${newRef['name']}`
                    window.$userPic = `${newRef['pic']}`;
                    console.log(window.$userPic, newRef['pic'])
                    if (window.$userPic !== '' && window.$userPic !== undefined && newRef['pic'].replaceAll(/\s/g,'')!==''){
                      (document.getElementById('user_pic') as HTMLImageElement).src = window.$userPic

                    }else{
                      (document.getElementById('user_pic') as HTMLImageElement).src = person
                    }

                    // console.log(window.$userPic)


                  }else{
                    console.log('Go back')
                  }
                })
            }
        });

    
        
        return () => AuthCheck();
    }, [auth]);
    if (loading) return <div>Main</div>;

    return <>{children}</>;
};
export default AuthRoute;
